@font-face {
  font-family: "Roboto";
  src: url("../fonts/../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

/* Reset styles */

* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

nav ul {
  list-style: none;
}

img {
  vertical-align: top;
}

nav,
footer,
header,
aside,
label {
  display: block;
}

input,
button,
textarea {
  font-family: inherit;
  color: inherit;
}

::placeholder {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

.hide {
  opacity: 0;
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #D4DF6D;
  z-index: 100;
  display: grid;
  place-content: center;
}

.loader {
  display: grid;
  place-content: center;
  width: 125px;
  height: 125px;
  border: solid 10px #537D13;
  border-radius: 50%;
  border-left-color: #F6E791;
  animation: load 1.3s linear infinite;
}

@keyframes load {
  100% {
    transform: rotate(360deg);
  }
}

/* global styles*/

body {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.27;
  color: #483C08;
  background-color: #fff;
}

:root {
  scroll-behavior: smooth;
}

.no-scroll {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1170px;
  padding: 0 15px;
  border: solid 1px red;
}

.title {
  margin: 0 auto;
  font-weight: 700;
  font-size: clamp(32px, 1.1071rem + 4.4643vw, 52px);
  text-align: center;
}

.header {
  position: absolute;
  top: 27px;
  left: 0;
  width: 100%;
  height: 113px;
  z-index: 3;
}

.header__inner {
  width: 100%;
  height: 113px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.logo {
  width: clamp(200px, 8.7059rem + 18.9706vw, 329px);
}

.logo__img {
  width: 100%;
  height: 100%;
}

.nav__link {
  font-weight: 700;
  font-size: 20px;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: #483C08;
  text-transform: uppercase;
  transition: all 0.3s;
}

.burger {
  width: 24px;
  height: 16px;
  position: relative;
  transition: all 0.3s;
  z-index: 10;
}

.burger__span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2.2px;
  border-radius: 1px;
  background-color: #483C08;
  transition: all 0.3s;
}

.burger__span-one {
  top: 0;
}

.burger__span-two {
  top: 50%;
}

.burger__span-three {
  top: 100%;
}

.hero {
  position: relative;
  width: 100%;
  padding: 156px 0 100px 0;
  background-color: #fff;
  background-image: url("../img/hero-bg.webp");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.hero__overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1;
}

.hero__title {
  position: relative;
  max-width: 578px;
  color: #F9E994;
  z-index: 2;
}

.hero__inner {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  z-index: 2;
}

.hero__bg {
  width: 100%;
  max-width: 710px;
  position: absolute;
  left: 50%;
  z-index: 3;
  pointer-events: none;
}

.hero__bg-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero__content {
  position: relative;
  width: 100%;
  max-width: 439px;
  padding-top: 34px;
  z-index: 2;
}

.hero__content-text {
  font-weight: 700;
  line-height: 1.3;
  color: #f6f7e4;
  text-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.25);
  margin-bottom: 60px;
}

.hero__list {
  width: 100%;
  max-width: 323px;
  background-color: #7fb3e1;
  border-radius: 20px;
  list-style: none;
}

.hero__list-item {
  position: relative;
  padding-left: 35px;
  font-weight: 700;
  font-size: 22px;
  color: #dfe4de;
}

.hero__list-item:not(:last-child) {
  margin-bottom: 14px;
}

.hero__list-item::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  translate: 0 -50% 0;
  width: 18px;
  height: 24px;
  background-image: url("../img/black-paw.webp");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.order {
  position: relative;
  width: 100%;
  max-width: 270px;
  padding-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  background: #fdfcfc;
  opacity: 0.7;
  overflow: hidden;
  z-index: 2;
}

.order__top {
  width: 100%;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  margin-bottom: 21px;
}

.order__top-inner {
  width: 100%;
  padding: 23px 24px 28px 24px;
  background-color: #7fb3e1;
  clip-path: polygon(0 0, 100% 0, 100% 90%, 50% 100%, 0 90%);
}

.order__title {
  font-weight: 700;
  font-size: 33px;
  text-align: center;
  color: #483c08;
  text-transform: uppercase;
  margin-bottom: 4px;
}

.order__time {
  font-weight: 700;
  font-size: 36px;
  color: #f6e791;
  text-align: center;
  min-height: 47px;
}

.order__discount {
  width: 100%;
}

.order__old-price {
  font-weight: 700;
  font-size: 30px;
  color: #6b6b6b;
  margin-right: 15px;
  position: relative;
}

.order__old-price::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: 0 -50% 0;
  rotate: 163deg;
  width: 100%;
  height: 2px;
  background-color: #F6BE91;
}

.order__new-price {
  font-weight: 700;
  font-size: 36px;
  color: #483c08;
}

.order-form {
  width: 100%;
  padding: 0 24px;
}

.order-form__input-box {
  position: relative;
  width: 100%;
  height: 50px;
  margin-bottom: 15px;
}

.order-form__input-box::before {
  content: "";
  position: absolute;
  left: 16px;
  top: 50%;
  translate: 0 -50% 0;
  width: 24px;
  height: 24px;
  background-image: url("../img/grey-paw.webp");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.order-form__input {
  width: 100%;
  height: 100%;
  padding: 0 10px 0 55px;
  border-radius: 30px;
  background-color: rgba(243, 239, 239, 0.9);
  font-weight: 700;
  font-size: 16px;
  line-height: 1;
  color: #777;
}

.order-form__input::placeholder {
  font-weight: 700;
  font-size: 16px;
  color: #777;
}

.order-form__button {
  width: 100%;
  padding: 7px 63px;
  margin-bottom: 30px;
  border-radius: 30px;
  font-weight: 700;
  font-size: 30px;
  text-transform: uppercase;
  color: #fff;
  background-color: #A1D337;
}

.order__date {
  width: 100%;
  font-weight: 400;
  font-size: 18px;
  text-align: center;
  color: #444;
  text-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.manufacturer__title {
  background: linear-gradient(180deg, #f9e994 0%, #537d13 56.25%, #483c08 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.manufacturer__inner {
  width: 100%;
  min-height: 439px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.manufacturer__card {
  width: 100%;
  max-width: 305px;
  min-height: 439px;
  padding: 15px;
  border-radius: 20px;
  background-color: #F6E791;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.manufacturer__card-img {
  max-width: 100%;
}

.manufacturer__card-content {
  width: 100%;
  min-height: 218px;
  padding: 21px 16px 23px;
  border-radius: 20px;
  background-color: rgba(161, 211, 55, 0.4);
}

.manufacturer__card-title {
  font-weight: 700;
  color: #fff;
  text-align: center;
  margin-bottom: 19px;
}

.manufacturer__card-text {
  text-align: center;
  color: #483C08;
}

.calm {
  width: 100%;
  background-image: linear-gradient(180deg, #e1edf8 0%, rgba(127, 179, 225, 0) 59.38%, rgba(0, 0, 0, 0.25) 100%), url("../img/calm-img2.webp");
  background-size: contain;
  background-position: center bottom;
  background-repeat: no-repeat;
}

.calm__title {
  color: #537D13;
}

.calm__inner {
  width: 100%;
  padding: 0 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.calm__text:not(:last-child) {
  margin-bottom: 25px;
}

.calm__subtitle {
  display: block;
  margin-bottom: 25px;
}

.calm__img-box {
  width: 100%;
  max-width: 300px;
  aspect-ratio: 1/1;
}

.calm__img {
  width: 100%;
  height: 100%;
}

@media (min-width: 320px) {
  .nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100dvh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    translate: 100% 0 0;
    padding: 100px 0 0 0;
    background-color: #D4DF6D;
    z-index: 10;
    overflow: hidden;
  }

  .nav__list {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 51px;
  }

  .burger {
    display: block;
  }

  .untranslate {
    translate: 0 0 0;
  }

  .span-one-transform {
    top: 50%;
    transform: rotate(45deg);
    transition: all 0.3s;
  }

  .span-two-transform {
    transform: rotate(-45deg);
    transition: all 0.3s;
  }

  .span-three-transform {
    display: none;
  }

  .hero__inner {
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding-top: clamp(180px, 0.0893rem + 55.8036vw, 430px);
  }

  .hero__bg {
    translate: -50% 0 0;
    top: 20px;
  }

  .hero__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .hero__content-text {
    margin-bottom: 20px;
    font-size: 20px;
    text-align: center;
  }

  .hero__list {
    padding: 45px 10px 27px 27px;
  }

  .hero__list-item {
    font-size: 20px;
  }

  .manufacturer {
    width: 100%;
    padding: 50px 0;
  }

  .manufacturer__title {
    margin-bottom: 30px;
  }

  .manufacturer__inner {
    flex-direction: column;
  }

  .manufacturer__card-title {
    font-size: 20px;
  }

  .calm {
    padding: 40px 0 60px 0;
  }

  .calm__inner {
    padding: 0;
    flex-direction: column;
  }

  .calm__content {
    border: 10px solid #D4DF6D;
    border-radius: 10px;
    padding: 10px;
    order: 2;
  }

  .calm__title {
    margin-bottom: 20px;
  }

  .calm__img-box {
    order: 1;
    max-width: 100%;
  }
}

@media (min-width: 600px) {
  .calm {
    padding: 40px 0 100px 0;
  }

  .calm__img-box {
    max-width: 80%;
  }
}

@media (min-width: 768px) {
  .nav {
    position: static;
    height: 100%;
    width: clamp(360px, 4.7069rem + 37.069vw, 446px);
    background-color: transparent;
    background-image: url("../img/nav-bg.webp");
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    border: 1px solid red;
    padding: 0;
    translate: 0 0 0;
  }

  .nav__list {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 51px;
  }

  .burger {
    display: none;
  }

  .hero__inner {
    flex-direction: row;
  }

  .hero__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .hero__content-text {
    margin-bottom: 60px;
    font-size: 26px;
    text-align: left;
  }

  .hero__list {
    padding: 45px 30px 27px 44px;
  }

  .hero__list-item {
    font-size: 20px;
  }

  .manufacturer {
    padding: 100px 29px;
  }

  .manufacturer__title {
    margin-bottom: 60px;
  }

  .manufacturer__inner {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .manufacturer__card-title {
    font-size: 22px;
  }

  .calm__content {
    width: 100%;
    max-width: 760px;
    padding: 40px 98px 40px 40px;
    background-image: url("../img/calm-img3.webp");
    background-size: 100% 100%;
    background-position: center center;
    background-repeat: no-repeat;
    border: none;
  }

  .calm__title {
    margin-bottom: 58px;
  }

  .calm__img-box {
    max-width: 60%;
  }
}

@media (min-width: 1100px) {
  .calm {
    padding: 99px 0 197px 0;
  }

  .calm__inner {
    flex-direction: row;
  }

  .calm__content {
    order: 1;
  }

  .calm__img-box {
    max-width: 300px;
    order: 2;
  }
}

@media (min-width: 1140px) {
  .nav__item {
    translate: 20px 0 0;
  }

  .hero__inner {
    padding-top: 0;
  }

  .hero__bg {
    translate: -46% -98% 0;
    top: 100%;
  }

  .calm__inner {
    padding: 0 30px;
  }
}